import { NextResponse } from "next/server";
import fs from "fs/promises";

export async function GET() {
  try {
    const API_KEY = process.env.API_KEY;
    if (!API_KEY) {
      return NextResponse.json({ error: "API_KEY is missing" }, { status: 500 });
    }

    const response = await fetch(
      `https://generativelanguage.googleapis.com/v1beta/models/gemini-pro:generateContent?key=${API_KEY}`,
      {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          contents: [
            {
              parts: [
                {
                  text: `Generate 3 blog posts as JSON array. 
Format:
[
  { "title": "Blog Title", "slug": "blog-title", "content": "Blog content..." }
]
Return ONLY the JSON.`,
                },
              ],
            },
          ],
        }),
      }
    );

    const data = await response.json();

    const rawText = data?.candidates?.[0]?.content?.parts?.[0]?.text || "";
    // log raw text into file for debugging
    await fs.writeFile("blogdata/raw.json", rawText, "utf-8");

    return NextResponse.json({
      success: true,
      message: "Fetched raw response (check blogdata/raw.json)",
      raw: rawText,
    });
  } catch (err) {
    return NextResponse.json({ error: err.message }, { status: 500 });
  }
}
